import * as React from "react";
import { Component } from "react";

import keydown, { ALL_KEYS } from "react-keydown";

import { updatePlayerHP, updateEnemyHP } from "reduxStore/actions";

import { QuestionInterface } from "utils/types";

interface Props {
  isOpen: boolean;
  isCorrect: boolean;
  question: QuestionInterface;
}

interface State {
  isOpen: boolean;
}

class Question extends Component<Props, State> {
  constructor(props) {
    super(props);

    this.dismissModal = this.dismissModal.bind(this);
  }

  state = {
    isOpen: this.props.isOpen
  };

  componentWillReceiveProps(nextProps: Props) {
    if (nextProps.isOpen !== this.props.isOpen) {
      this.setState(prevState => ({
        ...prevState,
        isOpen: nextProps.isOpen
      }));
    }
  }

  @keydown(ALL_KEYS)
  dismissModal() {
    if (this.props.isCorrect) {
      this.props.dispatch(updateEnemyHP(-10));
    } else {
      this.props.dispatch(updatePlayerHP(-10));
    }

    this.setState(prevState => ({
      ...prevState,
      isOpen: false
    }));
  }

  render() {
    return (
      <div
        className={`correct-modal-outer ${!this.state.isOpen ? "hide" : ""}`}
        onKeyDown={this.dismissModal}
      >
        <div className="correct-modal-inner">
          <h1>{this.props.isCorrect ? "צדקת!" : "טעית!"}</h1>
          <p>
            במשפט <span>{this.props.question.sentence}</span>
          </p>
          <p className="big">
            ה{this.props.question.answerLabel} הוא{" "}
            <span>{this.props.question.correctAnswer}</span>
          </p>
          <p className="call-to-action">לחץ על כל מקש בכדי להמשיך</p>
        </div>
        <style jsx>{`
          .correct-modal-outer {
            position: absolute;
            min-height: 100%;
            min-width: 100%;
            background: rgba(0, 0, 0, 0.4);
            z-index: 2;
          }

          .correct-modal-outer.hide {
            display: none;
          }

          .correct-modal-inner {
            direction: rtl;
            text-align: center;
            background: #fff;
            width: 90%;
            height: 70%;
            padding: 2rem 4rem;
            margin: 10rem auto;
            border-radius: 2px;
            box-shadow: 0 0 6px 3px rgba(0, 0, 0, 0.15);
          }

          .correct-modal-inner h1 {
            font-size: 2.5rem;
          }

          .correct-modal-inner p {
            font-size: 2.5rem;
            margin: 0;
          }

          .correct-modal-inner p span {
            font-size: 2.5rem;
            font-weight: bold;
          }

          .correct-modal-inner p.big {
            font-size: 3rem;
          }

          .correct-modal-inner p.big span {
            font-size: 3rem;
          }

          .correct-modal-inner p.call-to-action {
            font-size: 1.5rem;
            color: #9c9c9c;
            margin: 1rem 0 0;
          }
        `}</style>
      </div>
    );
  }
}

export default Question;
